<template>
  <div>
    <a-collapse ghost>
      <a-collapse-panel key="1" header="">
        <p> <span style="color: red;"></span></p>
      </a-collapse-panel>
    </a-collapse>
    <a-card class="card" v-for="(item, index) in model.infoWindows" :key="index">
      <close-circle-outlined class="show-circle" @click="remove(index)"/>
      <a-row class="margin-top">
        <a-col :span="labelSpan">
          <a-tooltip>
            <template #title>true</template>
            <div class="label"></div>
          </a-tooltip>
        </a-col>
        <a-col :span="wrapperSpan">
          <a-switch v-model:checked="item.visible" checked-children="" un-checked-children="" />
        </a-col>
      </a-row>
      <a-row class="margin-top">
        <a-col :span="labelSpan">
          <a-tooltip>
            <template #title>truecontentfalsecontent</template>
            <div class="label">div</div>
          </a-tooltip>
        </a-col>
        <a-col :span="wrapperSpan">
          <a-switch v-model:checked="item.isCustom" checked-children="" un-checked-children="" @change="forceRenderMap()" />
        </a-col>
      </a-row>
      <a-row class="margin-top">
        <a-col :span="labelSpan">
          <a-tooltip>
            <template #title></template>
            <div class="label"></div>
          </a-tooltip>
        </a-col>
        <a-col :span="wrapperSpan">
          <a-switch v-model:checked="item.autoMove" checked-children="" un-checked-children="" @change="forceRenderMap()" />
        </a-col>
      </a-row>
      <a-row class="margin-top">
        <a-col :span="labelSpan">
          <a-tooltip>
            <template #title></template>
            <div class="label"></div>
          </a-tooltip>
        </a-col>
        <a-col :span="wrapperSpan">
          <a-input-group compact>
            <a-input-number v-model:value="item.position[0]" style="width: 50%" />
            <a-input-number v-model:value="item.position[1]" style="width: 50%" />
          </a-input-group>
        </a-col>
      </a-row>
      <a-row class="margin-top">
        <a-col :span="labelSpan">
          <a-tooltip>
            <template #title>HTML</template>
            <div class="label"></div>
          </a-tooltip>
        </a-col>
        <a-col :span="wrapperSpan">
          <a-textarea v-model:value="item.content" :auto-size="{ minRows: 2 }"/>
        </a-col>
      </a-row>
      <a-row class="margin-top" v-if="!item.isCustom">
        <a-col :span="labelSpan">
          <a-tooltip>
            <template #title>isCustomtrue</template>
            <div class="label"></div>
          </a-tooltip>
        </a-col>
        <a-col :span="wrapperSpan">
          <a-input-group compact>
            <a-input-number v-model:value="item.size[0]" style="width: 50%" />
            <a-input-number v-model:value="item.size[1]" style="width: 50%" />
          </a-input-group>
        </a-col>
      </a-row>
      <a-row class="margin-top">
        <a-col :span="labelSpan">
          <a-tooltip>
            <template #title>'bottom-center''top-left'</template>
            <div class="label"></div>
          </a-tooltip>
        </a-col>
        <a-col :span="wrapperSpan">
          <a-select v-model:value="item.anchor" style="width: 100%" :options="anchorOptions"></a-select>
        </a-col>
      </a-row>
    </a-card>
    <div>
      <a-row>
        <a-col :span="6"></a-col>
        <a-col :span="6">
          <a-button type="link" @click="add" :disabled="model.infoWindows.length > 0">
            <template #icon><plus-outlined /></template>
          </a-button>
        </a-col>
        <a-col :span="6"></a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
export default {
  name: "LabelMarkersConfig",
  components: {},
  props: {
    model: {
      type: [Object],
      required: true
    },
    map: {
      type: [Object, null],
      required: true
    }
  },
  data () {
    return {
      labelSpan: 6,
      wrapperSpan: 18,
      anchorOptions: [
        { label: '', value: 'top-left' },
        { label: '', value: 'middle-left' },
        { label: '', value: 'bottom-left' },
        { label: '', value: 'top-center' },
        { label: '', value: 'center' },
        { label: '', value: 'bottom-center' },
        { label: '', value: 'top-right' },
        { label: '', value: 'middle-right' },
        { label: '', value: 'bottom-right' }
      ]
    }
  },
  methods: {
    add () {
      const item = {
        visible: true,
        position: this.model.map.center,
        isCustom: false,
        autoMove: false,
        content: '<div style="color:red;">hello~</div>',
        size: [null, null],
        anchor: 'bottom-center'
      }
      this.model.infoWindows.push(item)
    },
    remove (index) {
      this.model.infoWindows.splice(index, 1)
    },
    forceRenderMap () {
      this.model.map.render = false
      setTimeout(() => { this.model.map.render = true }, 1000) // 1
    }
  },
}
</script>

