<template>
  <div>
    <h4></h4>
    <a-row class="margin-top">
      <a-col :span="8">
        <a-button type="link" @click="preview"><template #icon><picture-outlined /></template></a-button>
      </a-col>
      <a-col :span="8">
        <a-popconfirm
            title="?"
            ok-text=""
            cancel-text=""
            @confirm="saveImage"
        >
          <a-button type="link"><template #icon><download-outlined /></template></a-button>
        </a-popconfirm>
      </a-col>
      <a-col :span="8">
        <a-popconfirm
            title="JSON?"
            ok-text=""
            cancel-text=""
            @confirm="saveJson"
        >
          <a-button type="link"><template #icon><export-outlined /></template></a-button>
        </a-popconfirm>
      </a-col>
    </a-row>
    <!---->
    <a-modal v-model:visible="previewModal" title="" :footer="null" :width="'848px'">
      <template v-if="previewImage"><a-image :width="'800px'" :src="previewImage"/></template>
      <template v-else><picture-outlined /></template>
    </a-modal>
  </div>
</template>

<script>
import {Screenshot} from "@amap/screenshot/dist"
import { time } from "@/utils/time"
import {
  PictureOutlined,
  DownloadOutlined,
  ExportOutlined
} from "@ant-design/icons-vue"

export default {
  name: "ExportTool",
  components: {
    PictureOutlined,
    DownloadOutlined,
    ExportOutlined
  },
  props: {
    map: {
      type: [Object, null],
      required: true
    },
    model: {
      type: [Object],
      required: true
    }
  },
  data () {
    return {
      previewModal: false,
      previewImage: null
    }
  },
  methods: {
    preview () {
      const screenshot = new Screenshot(this.map)
      screenshot.toDataURL().then(url => {
        this.previewImage = url
        this.previewModal = true
      })
    },
    saveImage () {
      const screenshot = new Screenshot(this.map)
      const filename = `Airmap_${time.timeStringFilename()}.jpg`
      screenshot.download({
        type: 'image/jpeg',
        filename: filename
      }).then(() => {
        this.$message.success('')
      }).catch((error) => {
        console.log(error)
      })
    },
    saveJson () {
      const text = JSON.stringify(this.model)
      console.log('text', text)
      const filename = `Airmap_${time.timeStringFilename()}.json`
      const blob = new Blob([text], { type: 'text/plain' })
      const href = URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = href
      link.download = filename
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      URL.revokeObjectURL(href)
    }
  }
}
</script>

