<template>
  <div>
    <h4></h4>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title>, 2D3D3D 3D </template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-switch
            v-model:checked="model.map.viewMode"
            checked-children="3D"
            un-checked-children="2D"
            checked-value="3D"
            un-checked-value="2D"
            @change="viewChange"
        />
      </a-col>
    </a-row>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title>adcodecitycode</template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-input v-model:value="cityName" @change="cityNameChange" placeholder=""></a-input>
      </a-col>
    </a-row>
    <a-row class="margin-top" v-if="cityName">
      <a-col :span="6">
        <a-tooltip>
          <template #title> mask </template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-switch
            v-model:checked="model.map.hasMask"
            checked-children=""
            un-checked-children=""
            @change="hasMaskChange"
        />
      </a-col>
    </a-row>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title></template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-input-group compact v-if="model.map.center">
          <a-input v-model:value="model.map.center[0]" style="width: 50%" />
          <a-input v-model:value="model.map.center[1]" style="width: 50%" />
        </a-input-group>
        <a-input-group compact v-else>
          <a-input disabled style="width: 50%" /><a-input disabled style="width: 50%" />
        </a-input-group>
      </a-col>
    </a-row>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title></template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-select v-model:value="model.map.mapStyle" style="width: 100%" :options="mapStyles"
        ></a-select>
      </a-col>
    </a-row>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title></template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-slider v-model:value="model.map.zoom" :min="2" :max="20" :step="0.01"/>
      </a-col>
    </a-row>
    <a-row class="margin-top" v-if="model.map.viewMode === '3D'">
      <a-col :span="6">
        <a-tooltip>
          <template #title> 0 zoom 2D </template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="14">
        <a-slider v-model:value="model.map.pitch" :min="0" :max="90"/>
      </a-col>
      <a-col :span="3">
        <a-input-number v-model:value="model.map.pitch" :bordered="false" :min="0" :max="90" :controls="false" />
      </a-col>
    </a-row>
    <a-row class="margin-top" v-if="model.map.viewMode === '3D'">
      <a-col :span="6">
        <a-tooltip>
          <template #title>3D </template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="14" class="label">{{ model.map.skyColor }}</a-col>
      <a-col :span="3">
        <color-selector v-model:value="model.map.skyColor" size="small" @change="forceRenderMap"></color-selector>
      </a-col>
    </a-row>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title> [0-360] 0</template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="14">
        <a-slider v-model:value="model.map.rotation" :min="0" :max="360"/>
      </a-col>
      <a-col :span="3">
        <a-input-number v-model:value="model.map.rotation" :bordered="false" :min="0" :max="360" :controls="false" />
      </a-col>
    </a-row>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title> POI  true</template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-switch v-model:checked="model.map.showLabel" checked-children="" un-checked-children="" @change="forceRenderMap"/>
      </a-col>
    </a-row>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title>, 'bg''point'POI'road''building'['bg','point','road','building']</template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-checkbox-group v-model:value="model.map.features" name="features" :options="featureOptions" />
      </a-col>
    </a-row>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title></template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-switch v-model:checked="model.map.showScale" checked-children="" un-checked-children="" />
      </a-col>
    </a-row>
    <a-row class="margin-top">
      <a-col :span="6">
        <a-tooltip>
          <template #title></template>
          <div class="label"></div>
        </a-tooltip>
      </a-col>
      <a-col :span="17">
        <a-switch v-model:checked="model.map.showHawkeye" checked-children="" un-checked-children=""/>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import ColorSelector from "../../components/V3ColorPicker/V3ColorPicker.vue"
export default {
  name: "MapConfig",
  components: {
    ColorSelector
  },
  props: {
    model: {
      type: [Object],
      required: true
    },
    map: {
      type: [Object, null],
      required: true
    }
  },
  data () {
    return {
      cityName: '',
      featureOptions: [
        { label: '', value: 'bg' },
        { label: 'POI', value: 'point' },
        { label: '', value: 'road' },
        { label: '', value: 'building' }
      ],
      mapStyles: [
        { label: '', value: 'amap://styles/normal' },
        { label: '', value: 'amap://styles/dark' },
        { label: '', value: 'amap://styles/light' },
        { label: '', value: 'amap://styles/whitesmoke' },
        { label: '', value: 'amap://styles/fresh' },
        { label: '', value: 'amap://styles/grey' },
        { label: '', value: 'amap://styles/graffiti' },
        { label: '', value: 'amap://styles/macaron' },
        { label: '', value: 'amap://styles/blue' },
        { label: '', value: 'amap://styles/darkblue' },
        { label: '', value: 'amap://styles/wine' }
      ]
    }
  },
  methods: {
    viewChange (e) {
      this.model.map.viewMode = e
      this.forceRenderMap()
    },
    cityNameChange (e) {
      this.map.setCity(e.target.value)
    },
    hasMaskChange (value) {
      if (!value) {
        this.model.map.mask = null
        this.forceRenderMap()
        return
      }
      if (!this.cityName) {
        this.model.map.hasMask = false
        this.$message.info('')
        return
      }

      var _this = this
      this.map.plugin('AMap.DistrictSearch', function () {
        var district = new AMap.DistrictSearch({
          subdistrict: 0,
          extensions: 'all',
          level: 'city'
        })
        district.search(_this.cityName, function(status, result) {
          const bounds = result.districtList[0].boundaries;
          const mask = [];
          for (let i =0; i < bounds.length; i += 1) {
            mask.push([bounds[i]])
          }
          _this.model.map.mask = mask
          _this.forceRenderMap()
        })
      })
    },
    forceRenderMap () {
      this.model.map.render = false
      setTimeout(() => { this.model.map.render = true }, 1000) // 1
    }
  }
}
</script>

<style scoped>
.label {
  margin-top: 5px;
}
.margin-top {
  margin-top: 10px;
}
</style>
